header {
    background-color: rgb(249, 215, 78);
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 60px;
    gap: 60px
}

header div.logoContainer {
    display: flex;
    width: 125px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

header .logoImg {
    height: 60px;
}

header .logoText {
    margin: 0;
    font-size: 22px;
    font-weight: 600;
    line-height: 23px;
}

header i {
    cursor: pointer;
    font-size: 19px;
}

.search {
    width: 400px;
    min-width: 200px;
}

.search input {
    width: calc(100% - 40px);
    height: 45px;
    background-color: rgba(255, 255, 255, 0.616);
}

